</style>

<template>
  <div>
    <el-row>
      Расписание приема, форма
      <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="120px" class="demo-ruleForm">
        <el-form-item label="Врач" prop="doctor">
          <el-select v-model="ruleForm.doctor" placeholder="Врач">
            <el-option label="Дроздов В.В" value="1"></el-option>
            <el-option label="Пархоменко А.Ю" value="2"></el-option>
            <el-option label="Баранова Н.М." value="3"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="№ кабинета" prop="room">
          <el-select v-model="ruleForm.room" placeholder="№ кабинета">
            <el-option label="Кабинет №1" value="1"></el-option>
            <el-option label="Кабинет №3" value="2"></el-option>
            <el-option label="Кабинет №4" value="3"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="Задать период?" prop="period">
          <el-switch v-model="ruleForm.period"  active-text="Да"
  inactive-text="Нет">></el-switch>
        </el-form-item>
        <el-form-item label="Период" v-if="ruleForm.period">
          <el-date-picker
            v-model="ruleForm.type"
            type="daterange"
            align="right"
            unlink-panels
            range-separator="До"
            start-placeholder="Дата начала"
            end-placeholder="Дата окончания"
            format="d.MM.yyyy"
            :picker-options="pickerOptions2">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="Дни недели" prop="week_day" v-if="ruleForm.type !== 'dates' && ruleForm.period"> 
          <el-checkbox-group v-model="ruleForm.week_day">
            <el-checkbox label="Пн" name="1"></el-checkbox>
            <el-checkbox label="Вт" name="2"></el-checkbox>
            <el-checkbox label="Ср" name="3"></el-checkbox>
            <el-checkbox label="Чт" name="4"></el-checkbox>
            <el-checkbox label="Пт" name="5"></el-checkbox>
          </el-checkbox-group>
        </el-form-item>
        <el-form-item label="Дата" v-if="!ruleForm.period">
            <el-form-item prop="date1">
              <el-date-picker type="date" placeholder="Выберите дату" :picker-options="pickerOptions1"> v-model="ruleForm.date1" format="d.MM.yyyy"></el-date-picker>
            </el-form-item>
        </el-form-item>
        <el-row>
          <el-col :span="12">
            <el-form-item label="Время приема" prop="resource">
              <el-col :span="11">
                <el-time-select
                  placeholder="Время начала"
                  v-model="ruleForm.startTime"
                  :picker-options="{
                    start: '08:30',
                    step: '00:15',
                    end: '18:30'
                  }">
                </el-time-select>
              </el-col>
              <el-col class="line" :span="2">-</el-col>
              <el-col :span="11">
                <el-time-select
                  placeholder="Время окончания"
                  v-model="ruleForm.endTime"
                  :picker-options="{
                    start: '08:30',
                    step: '00:15',
                    end: '18:30',
                    minTime: ruleForm.startTime
                  }">
                </el-time-select>
              </el-col>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="Интервал приема" prop="interval" label-width="170px">
              <el-input-number v-model="ruleForm.interval" step="5" controls-position="right" :min="5" :max="60"></el-input-number>
            </el-form-item>
          </el-col>
        </el-row>
        <el-form-item>
          <el-button type="primary" @click="submitForm('ruleForm')">Создать</el-button>
          <el-button @click="resetForm('ruleForm')">Сбросить</el-button>
        </el-form-item>
      </el-form>
      <!-- list -->
    <el-table :data="list" border style="width: 100%">
      <el-table-column prop="number" label="Номер">
      </el-table-column>
      <el-table-column prop="date_add" label="Номер">
      </el-table-column>
      <el-table-column prop="doctor" label="Врач">
      </el-table-column>
      <el-table-column prop="pinyin" label="Дата с">
      </el-table-column>
      <el-table-column prop="barcode" label="Дата до">
